@keyframes ripple-opacity-in {
  from {
    opacity: 0;
    animation-timing-function: linear;
  }

  to {
    opacity: 1;
  }
}

// ä»ä¸éæå°éæ
@keyframes ripple-opacity-out {
  from {
    opacity: 1;
    animation-timing-function: linear;
  }

  to {
    opacity: 0;
  }
}



