@mixin crumb-custom($font-size: null, $crumb-divider: null) {
  @if ($font-size) {
    font-size: $font-size;
  }
  @if ($crumb-divider) {
    .crumb-item {
      + .crumb-item::before {
        content: "#{$crumb-divider}";
      }
    }
  }
}



